import { Link } from 'react-router-dom';
import errorImg from '../../resources/img/error_img.png'; // Замените на путь к вашей картинке

const ErrorPage = () => {
    return (
        <div style={styles.container}>
            <img src={errorImg} alt="404 Error" style={styles.image} />
            <h1 style={styles.title}>Oops! Page not found</h1>
            <p style={styles.text}>We can't seem to find the page you're looking for.</p>
            <Link to="/" style={styles.link}>Go back to the homepage</Link>
        </div>
    );
};

const styles = {
    container: {
        textAlign: 'center',
        padding: '50px'
    },
    image: {
        width: '300px',
        marginBottom: '20px'
    },
    title: {
        fontSize: '2em',
        margin: '20px 0'
    },
    text: {
        fontSize: '1.2em',
        marginBottom: '30px'
    },
    link: {
        textDecoration: 'none',
        color: '#007BFF',
        fontSize: '1.5em',
    }
};

export default ErrorPage;